body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}


.center {
    text-align: center;
}

a {
    color:#636363;
    text-decoration: none;
    transition:all 0.5s ease-in;
}


a:hover {
    text-decoration: underline;
}


.App{
    background: #eee;
    width: 90%;
    margin:auto;
    margin-top: 10px;
    height: 90vh;
}

/* Navigation */
.navbar {
    text-align:center;
    margin: auto;
    display:flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px dotted grey;
    padding: 10px;
}


.navbar h2 {
    margin:0;
    font-weight:200;
    font-size:30px;
    letter-spacing: 4px;
    font-family:'Helvetica',sans-serif;
}

/*container*/

.container{
    padding: 10px;
    width: 90%;
    height: 80vh;
    display: flex;
    flex-direction: row;
    margin: auto;
}


.post-heading{
    color: green;
    font-size: 1.2em;
    text-align: center;
    padding: 10px;
}

/* Post Styles */
.post-container {
    margin: auto;
    padding: 10px;
    height: 100%;

}

.post_heading {
    text-align:center;
    font-weight:400;
    font-size:20px;
    color:#f5f5f5;    
}

.post_heading h1{
    text-align: center;
}

/* All Posts */
.allposts{
    padding:10px;
    overflow: scroll;
    border-left: 1px solid grey;
    width: 70%;
}

.all-post-heading {
    color: blue;
    font-size: 1.2em;
    text-align: center;
    padding: 10px;
}


.post{
    margin-top: 10px;
    padding: 10px;
    background:#f6f6f6;
    color: green;
    box-shadow: 5px 5px 5px 0 rgba(20, 20, 20, 0.09);
    border-radius: 10px;

}

.post_title {
    font-size:20px;
    font-family: sans-serif;
    color: grey;
    border-bottom: 1px dotted cyan;
    word-wrap: break-word;
} 


.post_message {
    font-size:15px;
    font-weight: 200;
    line-height: 2.5rem;
    word-wrap: break-word
}


.form {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form input {
    height:44px;
    padding-left:15px;
    padding-right:15px;
    border: 1px solid #dbe2e8;
    font-size:14px;
    border-radius:2px;
    color:#636363;
    box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
    outline:none;
    width:80%;
}

.form textarea {
    width:80%;
    max-width:80%;
    padding-left:15px;
    padding-right:15px;
    padding-top:15px;
    border: 1px solid #dbe2e8;
    font-size:14px;
    border-radius:2px;
    color:#636;
    font-family: 'Work Sans,',sans-serif;
    box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
    outline:none;
}


.form button {
    background:blue;
    padding:5px 10px;
    text-align: center;
    color:#fff;
    font-family:'Work Sans',sans-serif;
    border-radius:4px;
    border:none;
    font-size:15px;
}


.edit {
    background:green;
    border:none;
    text-align: center;
    font-size:10px;
    padding:5px 13px;
    border-radius:10px;
    cursor:pointer;
    margin-right:auto;

    outline: none;
    color:#fff;
}

.delete {
    float:right;
    background:red;
    border:none;
    text-align: center;
    font-size:10px;
    padding:5px 13px;
    border-radius:10px;
    cursor:pointer;
    margin-right:auto;
    outline: none;
    color:#fff;
}